;(function(){
  angular.module('triflix')
  .component('triflixBoard', {
    bindings: {
      index: '@'
    },
    templateUrl: 'app/components/triflix-board/triflix-board.html',
    controller: triflixBoardCtrl,
    controllerAs: 'board'
  });

  triflixBoardCtrl.$inject = ['$element', '$compile', '$scope', 'Game',
  '$rootScope', '$uibModal', 'TEAMS', 'SocketService'];

  function triflixBoardCtrl($element, $compile, $scope, Game, $rootScope, $uibModal,
    TEAMS, SocketService){
    var game, previous, self = this;

    this.$onInit = function(){
      game = Game.getStatus()[this.index];
      this.game = game;
      previous = angular.copy(game);
      if(SocketService.getOpponent()){
        SocketService.on('make move', function(opponentMove){
          $scope.$apply(function(){
            game.state[opponentMove] = game.team === TEAMS.X ? TEAMS.O : TEAMS.X;
          })
        });
      }
    }
    this.$doCheck = function(){
      if(!_.isEqual(game.state, previous.state)){

      }
    }

    this.makeMove = function(x, y){
      var flatCoordinate = Game.flatCoordinate(x, y);
      if(Game.lockBoard){
        return;
      }
      if(game.state[flatCoordinate] === TEAMS.EMPTY){
        game.state[flatCoordinate] = game.team;
        Game.opponentMove(game, flatCoordinate)
        .then(function(resp){
          _.extend(game, resp.data);
          if(game.winner.team){
            $rootScope.$emit('triflix.game.victory');
            var victoryModal = $uibModal.open({
              animation: true,
              component: 'victoryModal',
              resolve: {
                game: [function(){
                  return self.game;
                }]
              }
            })
            victoryModal.result.catch(function(dismissed){
              Game.restart();
              $rootScope.$emit('triflix.game.start');
            })
          }

        })
        .catch(function(error){
          console.log(error);
        })
      }
    }


    this.$postLink = function(){}


  }
})();

;(function(){
  angular.module('triflix')
  .component('panelContainer', {
    bindings: {
    },
    controller: panelContainerCtrl,
    controllerAs: 'panel'
  });

  panelContainerCtrl.$inject = ['$element', '$scope', '$rootScope', '$templateCache', '$compile',
   '$window', '$timeout'];

  function panelContainerCtrl($element, $scope, $rootScope, $templateCache, $compile,
    $window, $timeout){


      var width, height, leftPos;

      function initWindow(){
        width = window.innerWidth
        || document.documentElement.clientWidth
        || document.body.clientWidth;

        height = window.innerHeight
        || document.documentElement.clientHeight
        || document.body.clientHeight;
      }
      initWindow();

      this.$onInit = function(){
        $element.css('position', 'absolute');
        $element.css('width', width);
        $element.css('height', height);
        $element.css('top', '0px');
        $element.css('left', -width + 'px');
      }

      var panels = [];
      $rootScope.$on('triflix.addpanel', function(evt, params){
        var elem = angular.element('<div></div>');
        elem.css('float', 'left');
        elem.css('width', width);
        elem.css('height', height);

        if(params.controller ){
          var ctrl = angular.element('<div></div>');
          ctrl.attr('ng-controller', params.controller);
          var template = $templateCache.get(params.templateUrl);
          ctrl.append(angular.element(template));
          elem.append(ctrl);
        }
        else if(params.component){
          var component = '<' + params.component + '></' + params.component + '>';
          elem.append(component);
        }

        var newScope = $rootScope.$new();
        newScope.prototype = params.scope;
        newScope.$close = function(){
          var index = panels.indexOf(panel);

          panels.splice(index, 1);
          panel.scope().$destroy();


          var leftPos = panels.length ? -width * (panels.length - 1) : 0;
          /*$element.animate({
            left: leftPos
          }, 1000, function(){
            $timeout(function(){
              panel.remove();
              $element.css('width', width * panels.length);
            }, 3000);
          });*/
          $element.css('left', leftPos);
          $timeout(function(){
            panel.remove();
            $element.css('width', width * panels.length);
          }, 1000);

        }
        var panel = $compile(elem)(newScope);
        panels.push(panel);

        $element.parent().css('position', 'relative');
        $element.parent().css('overflow-x', 'hidden');
        $element.parent().css('width', width);
        $element.parent().css('height', height);

        $element.css('width', width * panels.length);
        $element.append(panel);
        var leftPos = -width * (panels.length - 1);
        /*$element.animate({
          left: leftPos
        }, 1000);*/
        $element.css('left', leftPos);

      });

      this.$postLink = function(){
        angular.element($window).on('resize', recalculateSize);
      }

      function recalculateSize(){
        initWindow();
        panels.forEach(function(panel){
          panel.css('height', height);
          panel.css('width', width);
        })

        $element.css('height', height);
        $element.css('width', width * panels.length);
        $element.css('left', (- width * (panels.length - 1)));
        $element.parent().css('height', height);
        $element.parent().css('width', width);
      }
  }
})();

